import { faCheck, faDotCircle } from "@fortawesome/free-solid-svg-icons";
import {
    faq_item,
    faq_question,
    open,
    chevron,
    faq_answer,
    container,
    faq_section,
    faq_content,
    faq_left,
    faq_iconand_h2,
    questions,
    faq_right,
    info_block,
    bullet_list,
    elementor_divider,
    elementor_divider_separator,
    contact_list,
    contact_item,
    stat_item,
    stats,
  } from "../../Styles/faq.module.css";
const questionsData = [
  {
    title: "Where Can I Find More Information?",
    value:
      "You can find more information in our documentation section or by contacting our support team directly.",
  },
  {
    title: "What Are Your Terms and Conditions?",
    value:
      "Our terms and conditions are designed to protect both our customers and our business. They cover usage rights, payment terms, and service agreements.",
  },
  {
    title: "What Kinds of Payment Do You Accept?",
    value:
      "We accept all major credit cards, PayPal, and bank transfers. For enterprise customers, we also offer invoice-based payments.",
  },
];

const info = [
  {
    title: "Tips & Information",
    value:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
    content: {
      type: "bullet",
      bullet: [
        { value: "Quis nostrud exercitation ullamco laboris" },
        { value: "Duis aute irure dolor in reprehenderit in voluptate" },
      ],
    },
  },
  {
    title: "Support",
    value:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusm tempor incididunt ut labore et dolore magna aliqua.",
    content: {
      type: "contact",
      contact: [
        { title: "Email Address", value: "support@domain.com", icon: "mail" },
        { title: "Phone Number", value: "(+62) 81 158 2158", icon: "phone" },
      ],
    },
  },
];

const data = [
  { title: "25+", subtitle: "Years Of Experience" },
  { title: "3,452+", subtitle: "Total Transaction" },
  { title: "751+", subtitle: "Active User" },
  { title: "592+", subtitle: "Positive Reviews" },
];

function FAQItem({ title, value }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className={faq_item}>
      <button
        className={`${faq_question} ${isOpen ? open : ""}`}
        onClick={() => setIsOpen(!isOpen)}
      >
        {title}
        <ChevronDown className={chevron} />
      </button>
      {isOpen && <div className={faq_answer}>{value}</div>}
    </div>
  );
}


function Faq() {
    return (
      <div className={"container"} id="faq">
        <section className={faq_section}>
          <div className={faq_content}>
            <div className={faq_left}>
              <div className={faq_iconand_h2}>
                <FontAwesomeIcon icon={faDotCircle} />
                <h2>FAQ</h2>
              </div>
              <h3>General Question</h3>
              <div className={questions}>
                {questionsData.map((q, i) => (
                  <FAQItem key={i} {...q} />
                ))}
              </div>
            </div>
  
            <div className={faq_right}>
              {info.map((item, i) => (
                <div key={i} className={info_block}>
                  <h3>{item.title}</h3>
                  <p>{item.value}</p>
                  {item.content.type === "bullet" && (
                    <>
                      <ul className={bullet_list}>
                        {item.content.bullet.map((b, j) => (
                          <li key={j}>
                            <FontAwesomeIcon icon={faCheck} />
                            {b.value}
                          </li>
                        ))}
                      </ul>
                      <div className={elementor_divider}>
                        <span className={elementor_divider_separator}></span>
                      </div>
                    </>
                  )}
  
                  {item.content.type === "contact" && (
                    <div className={contact_list}>
                      {item.content.contact.map((c, j) => (
                        <div key={j} className={contact_item}>
                          {c.icon === "mail" && <Mail />}
                          {c.icon === "phone" && <Phone />}
                          <div>
                            <h4>{c.title}</h4>
                            <p>{c.value}</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
  
          <div className={stats}>
            {data.map((item, i) => (
              <div key={i} className={stat_item}>
                <h3>{item.title}</h3>
                <p>{item.subtitle}</p>
              </div>
            ))}
          </div>
        </section>
      </div>
    );
  }

export default Faq;
