import {
  Button,
  Dialog,
  DialogPanel,
  DialogTitle,
  Transition,
} from "@headlessui/react";
import React, { Fragment, useState } from "react";
import {
  service_dialog_button,
  dialog_container,
  dialog_second_container,
  dialog_thired_container,
  dialog_panel,
  dialog_title,
  dialog_subtitle,
  dialog_button,
  dialog_content,
  dialog_title_and_subtitle,
  dialog_badge,
  service_dialog_close_icon,
  onEnter,
  onEnterFrom,
  leaveTo,
  onLeaveFrom,
} from "../../Styles/services.module.css";
import { faClose, faL } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
export default function ServiceDialog({ children, service }) {
  const [isOpen, setIsOpen] = useState(false);

  const open = () => setIsOpen(true);
  const close = () => setIsOpen(false);

  return (
    <>
      <Button onClick={open} className={service_dialog_button}>
        {children}
      </Button>

      <Transition appear show={isOpen} as={Fragment}>
        <Dialog as="div" className={dialog_container} onClose={close}>
          <div className={dialog_second_container}>
            <div className={dialog_thired_container}>
              <Transition.Child
                as={Fragment}
                enter={onEnter}
                enterFrom={onEnterFrom}
                leave={leaveTo}
                leaveFrom={onLeaveFrom}
                leaveTo={leaveTo}
              >
                <DialogPanel className={dialog_panel}>
                  <FontAwesomeIcon
                    icon={faClose}
                    onClick={close}
                    className={service_dialog_close_icon}
                  />
                  <img
                    src={"https://techzaa.in/techpopup/images/img-1.jpg"}
                    alt={service.title}
                    style={{ width: "100%", borderRadius: "12px" }}
                  />
                  <div className={dialog_content}>
                    <div className={dialog_title_and_subtitle}>
                      <div className={dialog_badge}>GET DISCOUNT</div>
                      <DialogTitle as="h3" className={dialog_title}>
                        {service.title}
                      </DialogTitle>
                      <p className={dialog_subtitle}>{service.description}</p>
                    </div>
                    <div className={dialog_button}>
                      <Button className="inline-flex items-center gap-2 rounded-md bg-gray-700 py-1.5 px-3 text-sm/6 font-semibold text-white shadow-inner shadow-white/10 hover:bg-gray-600 focus:outline-none">
                        Sign Up
                      </Button>
                    </div>
                  </div>
                </DialogPanel>
              </Transition.Child>
            </div>
          </div>
        </Dialog>
      </Transition>
    </>
  );
}
