import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import {hero_section,content,title, description, feature_list,feature_item,feature_icon, image_container,hero_image} from "../../Styles/aboutus.module.css";
import landing_about_us from "../../assets/Images/landing-about-us-9ae2daf1c0d316702587f9f090283a14.jpg"
const aboutUsData = [
  {
    title: "Flooring is not our business it’s an opportunity to make you glad.",
    subtitle:
      "Factory Direct Flooring in Meridian, ID comes highly recommended by locals for its full-service flooring solutions. This veteran-founded and operated business is praised for its competitive pricing and exceptional service. Whether you're looking for carpet, hardwood, or laminate, Factory Direct Flooring is trusted by the community for their expertise and reliability.",
    bullets: [
      "Factory Direct Flooring is located in Meridian, ID.",
      "Highly recommended by local residents.",
      "A veteran-founded and operated business.",
      "Offers full-service flooring solutions.",
      "Known for competitive pricing.",
      "Praised for exceptional customer service.",
    ],
    imageUrl: landing_about_us,
    type: "default",
  },
];

const Aboutus = () => {
  return (
    <div className="container" id='aboutus'>
      {aboutUsData.map((item, index) => (
        <section className={hero_section} key={index}>
          <div className={content}>
            <h3 className={title}>{item.title}</h3>
            <h4 className={description}>{item.subtitle}</h4>
            {item.bullets.length > 0 && (
              <div className={feature_list}>
                {item.bullets.map((point, idx) => (
                  <div className={feature_item} key={idx}>
                    <FontAwesomeIcon icon={faCheck} className={feature_icon} />
                    <span>{point}</span>
                  </div>
                ))}
              </div>
            )}
          </div>
          <div className={`${image_container} ${item.type === 'simple' ? 'no-bg' : ''}`}>
            <img
              src={item.imageUrl}
              alt="Modern living room with wooden flooring"
              className={hero_image}
            />
          </div>
        </section>
      ))}
    </div>
  );
};

export default Aboutus;
