import React, { useState } from 'react';

import {BeatLoader} from "react-spinners"

export const Subscribe = ({
  
  title,
  subtitle,
  placeholder = 'Enter your email',
}) => {
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
  
    try {
      await new Promise((resolve) => setTimeout(resolve, 1000)); // wait for 1 second
      setEmail('');
      console.log(loading, "this is loading inside timeout");
    } finally {
      setLoading(false);
    }
  };
  return (
    <div className="subscribe-container">
      <div className="subscribe-overlay">
        <div className="subscribe-content">
          <span className="subscribe-label">NEWSLETTER</span>
          <h2 className="subscribe-title">{title}</h2>
       
          <form onSubmit={handleSubmit} className="subscribe-form">
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder={placeholder}
              className="subscribe-input"
              required
            />
            <button type="submit" className="subscribe-button" disabled={loading}>
              {loading ? <BeatLoader size={10} color='white'  />: 'Subscribe'}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};
